.sidebar {
    width: 300px;
    padding: 25px;
    float: left;
}

.sidebar .favourite-frameworks {
    margin-top: 60px;
}
.sidebar h2 {
    color: #57ba86;
    font-weight: 600;
    font-size: 30px;
}

.sidebar .progress-bar { 
    background-color: #57ba86;
}

.sidebar .language-item {
    padding-top: 30px;
}

.sidebar .language-item p {
    font-size: 18px;
    color: #fff;
    font-weight: 500;
}

.info-body {
    flex: 1;
    background: #57ba86;
    padding: 25px 25px 100px 50px;
    text-align: left;
}

.info-body h2 {
    font-weight: 700;
    font-size: 30px;
}

.info-body p {
    font-weight: 600;
    font-size: 18px;
    line-height: 1.8;
    max-width: 1000px;
}

.info-body .profile-pic {
    margin: 30px 20px 30px 0;
}

.info-bottom-link {
    position: relative;
    margin: 20px 20px 0 0;
    display: inline-block;
}

.info-bottom-link button {
    font-size: 16px;
}

.info-bottom-link:hover button, .info-bottom-link:focus button, .info-bottom-link:active button {
    animation-name: buttonHover;
    animation-duration: 1s;
    background-color: #354852;
    color: #57ba86;
}

.info-bottom-link button {
    padding: 20px 20px 20px 50px;
}

.info-bottom-icon {
    position: absolute;
    top: 20px;
    left: 15px;
}

.text-wrapper {
    animation-name: fadeIn;
    animation-duration: 2s;
}

@keyframes buttonHover {
    0% 
    {
        background-color: #2b3a42;
        color: #fff;
    }
    100% 
    {
        background-color: #354852;
        color: #57ba86;
     }
}

@keyframes fadeIn {
    0% {opacity: 0};
    100% {opacity: 1};
}

.info-body .madeReact {
    position: absolute;
    bottom: 10px;
    text-decoration: none;
}
.info-body .madeReact p {
    font-size: 12px;
    color: #2b3a42;
}