@font-face {
  font-family: 'Roboto';
  src:local('Roboto'), url(./fonts/Roboto-Light.ttf) format('truetype');
}

html {
  background-color: #2b3a42;
}

html, body, #root {
  height: 100%;
}

body p, body h1, body h2 {
  font-family: Roboto;
}

h2 {
  margin: 0;
  font-size: 36px;
  font-weight: 700;
}

.page-wrapper {
  background: #2b3a42;
  width: 100%;
  position: relative;
  margin: 0;
  display: flex;
  min-height: 100%;
  width:100%;
}

.page-wrapper::after {
  content: "";
  background-image: url(./images/office-background.jpg);
  background-size: cover;
  opacity: 0.2;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  position: absolute;
  z-index: 1;  
}

.page-wrapper.page-info::after {
  content: none;
}

.centre-content {
  color: #57ba86;
  position: absolute;
  left: 110px;
  top: 50%;
  -ms-transform: translateY(-50%);
  transform: translateY(-50%);
  width: 70%;
  z-index: 2;
}

.centre-content h1{
  margin: 0;
  font-size: 48px;
  font-weight: 500;
}

.centre-content p {
  font-size: 30px;
  padding-top: 20px;
}

.code-text {
  color: #c8d4cd;
}

.divider {
  height: 6px;
  width: 100%;
  background: #57ba86;
}

.profile-links {
  margin-top: 30px;
}
.profile-links img {
  margin-right: 20px;
}

button {
  height: 60px;
  font-size: 30px;
  padding: 0 20px;
  border: 0px;
  cursor: pointer;
  background: #2b3a42;
  color: #FFF;
  font-weight: 600;
}

.hidden-input {
  display: none;
}

.react-toast-notifications__toast__icon-wrapper {
  background: #2b3a42 !important;
}
.react-toast-notifications__toast__countdown {
  background-color: rgba(0,0,0,0.5) !important;
}

.more-info-button {
  background: #57ba86;
  color: #FFF;
  font-size: 24px;
  margin-top: 15px;
}

.more-info-button:hover, .more-info-button:focus, .more-info-button:active {
  background: #63d498;
  outline: none;
}

@media all and (max-width: 600px) {
  .centre-content {
    left: 50px;
  }

  .centre-content h1 {
    font-size: 36px;
  }

  .centre-content p {
    font-size: 24px;
  }

  .more-info-button {
    font-size: 16px;
  }
}

@media all and (max-width: 420px) {
  .page-wrapper {
    display: block;
  }

  #root .sidebar {
    background-color: #2b3a42;
    float: initial;
    width: 100%;
  }

  #root .info-body {
    padding: 15px 15px 100px 30px
  }
}